.auth {
	max-width: 100vw;
	min-height: 90vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.auth__container {
	width: 30rem;
	height: 50rem;
	perspective: 100rem;
	-moz-perspective: 100rem;
	-webkit-perspective: 100rem;
	border-radius: 20px;
	transition: all 0.7s ease-out;
	margin-top: 5rem;
}

.auth__container--signup {
	height: 45rem;
	transition: all 1s ease;
	position: absolute;
	background: #ececec;
	text-align: center;
	width: 100%;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	border-radius: 2.5em;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	overflow: hidden;
}

.auth__container--login {
	height: 45rem;
	transition: all 1s ease;
	position: absolute;
	background: #ececec;
	width: 100%;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	border-radius: 2.5em;
	box-shadow: inset 0 0 15px rgba(55, 84, 170, 0),
		inset 0 0 20px rgba(255, 255, 255, 0), 7px 7px 15px rgba(55, 84, 170, 0.15),
		-7px -7px 20px white, inset 0px 0px 4px rgba(255, 255, 255, 0.2);
	overflow: hidden;
	text-align: center;
}

.auth__container--login {
	transform: rotateY(180deg);
}

.auth__container .auth__container--signup.flip {
	transform: rotateY(-180deg);
}
.auth__container .auth__container--login.flip {
	transform: rotateY(0);
}

.auth__signup--name,
.auth__signup--email,
.auth__signup--password,
.auth__login--email,
.auth__login--password {
	height: 40px;
	padding: 15px;
	border-radius: 12px;
	box-shadow: 5px 5px 10px #cdd1d9, -5px -5px 10px #ffffff;
	width: 20rem;
}

.auth__heading-signup,
.auth__heading-login {
	font-size: 48px;
	font-weight: 100;
	font-family: 'Montserrat', sans-serif;
	margin: 20px;
}

.auth__icon {
	color: #555;
	stroke-width: 1px;
}

.auth__back-link {
	position: absolute;
	margin: 3rem;
	border-radius: 13px;
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	background: #ececec;
	color: #000;
	font-size: 32px;
