.tabs-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  border: 4px solid #ebe8e8;
  border-radius: 20px;
  background: #ececec;
  box-shadow: 9px 9px 16px rgba(163, 177, 198, 0.6),
    -9px -9px 16px rgba(255, 255, 255, 0.5),
    inset 3px 3px 7px rgba(136, 165, 191, 0.48), inset -3px -3px 7px #ffffff;
  height: 60px;
  width: 490px;
}

.tabs__item {
  /* font-family: 'Itim', cursive; */
  font-size: 17px;
  font-weight: 400;
  height: 50px;
  width: 120px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.4s ease-in;
  cursor: pointer;
}

.tabs__item:active,
.active {
  background: #ececec;
  box-shadow: 10px 10px 20px #c9c9c9;
}

.tab-move {
  z-index: 1;
  display: block;
}

